import React from "react";
import { graphql } from 'gatsby'
import { GatsbyImage, getImage } from "gatsby-plugin-image"

import Layout from "../../../components/layout"
import Seo from "../../../components/seo"

const Pages = ({params, data}) =>{
    const image = getImage(data.markdownRemark.frontmatter.img)



    return params.frontmatter__type === "jeux" ?<Layout>
            <h1>{data.markdownRemark.frontmatter.titre}</h1>
            <GatsbyImage image={image} alt={`BanniÃ¨re du jeu ${data.markdownRemark.frontmatter.titre}` } />
            <div>
                <div
                    dangerouslySetInnerHTML={{ __html: data.markdownRemark.html }}
                />
            </div>
        </Layout>
        :false
}


export const query = graphql`
    query ($frontmatter__type: String, $frontmatter__jeux: String, $frontmatter__titre: String) {
    markdownRemark(
        frontmatter: {
            type: {eq: $frontmatter__type}, 
            jeux: {eq: $frontmatter__jeux}, 
            titre: {eq: $frontmatter__titre}
        }
    ) {
        html
        frontmatter {
        titre
        img {
            childImageSharp {
            gatsbyImageData(
                placeholder: BLURRED,
                formats: [AUTO, WEBP, AVIF],
                width: 720
                )
            }
        }
        }
    }
    }

`

export default Pages